import * as React from 'react';
import { Box, Container, Grid, Stack } from '@mui/material';
import Avatars from '../../../../components/material-ui/example/components/data-display/Avatars';

export default () => {
    return (
        <>
            <Box
                component="main"
                sx={{
                    flexGrow: 1,
                    py: 8
                }}
            >
                <Container maxWidth={false}>
                    <Stack spacing={3}>
                        {/* Stack     */}
                        <Avatars/>
                    </Stack>
                </Container>
            </Box>
        </>
    );
}
